 body{
   background-color: #333;
 }
 h1, h3, p{
   color: #fff;
   text-align: center;
 }
 p{
   height: 30px;
 }
 .contenedor{
   position: relative;
   display: flex;
   margin: 30px 0;
   justify-content: center;
   background-color: #000;
   padding: 80px 0px 10px 0px;
   max-width: 800px;
   margin: 0 auto;
   border-radius: 20px;
 }
 .contenedor:before{
  content: "";
  width: 95%;
  height: 20px;
  background-color: #4c5367;
  z-index: 5;
  position: absolute;
  top: 55px;
  border-radius: 10px 10px 0 0;
  border-bottom: 7px groove #8583a0
 }
 .titulo{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   color: #fff
 }
 .nota{
   position: relative;
   background-color: #fff;
   margin: 5px;
   border-radius: 5p;
   -webkit-border-radius: 5p;
   -moz-border-radius: 5p;
   -ms-border-radius: 5p;
   -o-border-radius: 5p;
   height: 400px;
   width: 100px;
}
.negra{
  background-color: #000;
  height: 250px;
  width:50px;
  border-radius:10px;
  -webkit-border-radius:10px;
  -moz-border-radius:10px;
  -ms-border-radius:10px;
  -o-border-radius:10px;
  position: absolute;
  right: -30px;
  z-index: 2;
}
.estaSonando{
  background-color: #999;
  transition: 1s;;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}
span{
  display: none;
  position: absolute;
  bottom: 20px;
  right: 35%;
  color: lightskyblue;
  font-size: 2em;
  font-weight: bold;
}
.show{
  display: block;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}